body{
  background: #30E8BF;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #FF8235, #30E8BF);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#FF8235), to(#30E8BF));
  background: -webkit-linear-gradient(top, #FF8235, #30E8BF);
  background: -o-linear-gradient(top, #FF8235, #30E8BF);
  background: linear-gradient(to bottom, #FF8235, #30E8BF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: calc(100vh);
  
    cursor: url("https://twxpl.innogamescdn.com/img/cursor/normal_1f58f6235f.cur"),url("data:image/png;base64,"),auto;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.container{
  
  margin:0 auto;
  max-width: 1200px;
}

.userlist{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;  
  margin-bottom: 120px;
  margin-top: 20px;
}
.user{
  padding:10px;
  -webkit-box-shadow: 1px 1px 5px #000;
          box-shadow: 1px 1px 5px #000;
  width: 200px;
  display: block;
  margin-bottom: 10px;
  margin-right: 10px;
  position: relative;
  text-align: center;
  background: url(https://abc-machine-embroidery.com/assets/images/Medieval_Splendor/Medieval_Dazzle_b.jpg) #fff top right;
  background-repeat: no-repeat;
  background-size: 90px;
}
.user-image{
  background-image: url(http://www.urltarget.com/images/border-braid-frame-plait-rope-circle.png);
  padding: 14px 0;
  background-size: contain;
  background-position: -10px;
  background-repeat: no-repeat;
  width: 124px;
  margin: auto;
}
.user-image img{
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}

.slave-count{
  text-align: right;
}

.fav-badge{
  position: absolute;
  top: 10px;
  right: 10px;  
  display: block;
  width: 50px;
  height: 50px;
  background: url(https://upload.wikimedia.org/wikipedia/commons/thumb/4/42/Coat_of_arms_of_the_Carmelite_order_%28simple%29.svg/1200px-Coat_of_arms_of_the_Carmelite_order_%28simple%29.svg.png) no-repeat center;
  background-size: contain;
  -webkit-filter: drop-shadow(0px 0px 5px #a41919);
          filter: drop-shadow(0px 0px 5px #a41919);
}

.search-form{
  background: #fff;
  width:100%;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}
.search-slave{
  background-color: transparent;
  border: 3px solid #bada55;
  padding: 10px 15px;
  display: inline-block;
}
.search-clear{
  padding: 10px 15px;
  display: inline-block;
  background: url(http://icongal.com/gallery/image/160560/actions_edit_clear.png) center;
      background-size: auto auto;
  background-size: cover;
  border: none;

}
.sort-slave{
  background-color: transparent;
  border: 3px solid #bada55;
  padding: 10px 15px;
  display: inline-block;
  margin-right: 15px;
}
.search-label{
  margin-right: 5px;
  margin-left: 15px;
}
.filter-slave{
  background-color: transparent;
  border: 3px solid #bada55;
  padding: 10px 15px;
}
.fancy-button{
  background: url("https://dspl.innogamescdn.com/8.115/36129/graphic/start2/button-big/middle.png");
  height: 38px;
  margin: 10px 0;
  position: relative;
  font-size: 14px;
line-height: 36px;
text-align: center;
color: #fff;
font-weight: bold;
}
.fancy-button:after{
  background: url("https://dspl.innogamescdn.com/8.115/36129/graphic/start2/button-big/right.png");
}
.fancy-button:before, .fancy-button:after {
  width: 10px;
  height: 38px;
}
.fancy-button:after {
  right: 0;
  top: 0;
}
.fancy-button:before, .fancy-button:after {
  content: '';
  position: absolute;
}
.fancy-button:before {
  background: url("https://dspl.innogamescdn.com/8.115/36129/graphic/start2/button-big/left.png");
}
.fancy-button:before {
  left: 0;
}


